import React from 'react';
import { connect, batch } from 'react-redux'
import { setDrinksPrefsView } from '../../../drinks/drinks-actions'
import { DRINKS_PREFS_VIEW_INGS, DRINKS_PREFS_VIEW_DRINKS_LIST } from '../../../drinks/drinks-reducer'
import './category-aside.scss'
import './category-aside-prefs.scss'

const stateToProps = state => {
    const drinks = state.drinks.drinks
    return {
        isShowingDrinksAllowed: drinks.length && state.drinks.origDrinks.length !== drinks.length,
        drinks,
        view: state.drinks.drinksPrefs.view
    };
};

function dispatchToProps(dispatch) {
    return {
        setDrinksPrefsIngsView: () => {
            batch(() => {
                dispatch(setDrinksPrefsView(DRINKS_PREFS_VIEW_INGS))
            })
        },
        setDrinksPrefsListView: () => {
            batch(() => {
                dispatch(setDrinksPrefsView(DRINKS_PREFS_VIEW_DRINKS_LIST))
            })
        }
    };
}

function CategoryAsidePrefs({ props, view, drinks, setDrinksPrefsIngsView, setDrinksPrefsListView, isShowingDrinksAllowed }) {

    const showBtn = (<button className='cat-aside-prefs' disabled={!isShowingDrinksAllowed}
        onClick={() => isShowingDrinksAllowed ? setDrinksPrefsListView() : false}>
        POKAÅ»
    </button>)

    const backBtn = (<button className='cat-aside-prefs'
        onClick={() => setDrinksPrefsIngsView()}>
        WRÃÄ DO PREFERENCJI?
    </button>)

    const btn = view === DRINKS_PREFS_VIEW_INGS
        ? showBtn
        : backBtn

    return (<div className='cat-aside-prefs'>
        <h3 className='cat-aside-prefs'>Liczba drinkÃ³w:</h3>

        <p className='cat-aside-prefs'>
            {drinks.length}
        </p>

        {btn}

    </div>)
}

export default connect(stateToProps, dispatchToProps)(CategoryAsidePrefs)