import { intersection, random } from 'lodash'
import { getUsableIngIds, getWantedIngIds, getNotWantedIngIds } from '../ings/ings'

function disableDrinksByIngs(drinks = [], ings = {}) {
    const notUsableIngIds = getUsableIngIds(ings, false)

    return drinks.map(d => ({
        ...d,
        ...{
            usable: !intersection(d.ings, notUsableIngIds).length
        }
    }))
}

function getRandomDrink(drinks = []) {
    const idx = random(drinks.length - 1);
    return drinks[idx]
}

function selectDrinks(drinks = [], ings = {}) {

    const usableIngs = getUsableIngIds(ings)
    console.log('usableIngs = ', usableIngs)

    const wantedIngs = getWantedIngIds(ings)
    console.log('wantedIngs = ', wantedIngs)

    const notWantedIngs = getNotWantedIngIds(ings)
    console.log('notWantedIngs = ', notWantedIngs)

    return drinks
        .map(d => {

            const isUsable = !!intersection(d.ings, usableIngs).length
            const isWanted = !wantedIngs.length || !!intersection(d.ings, wantedIngs).length
            const isNotNotWanted = !intersection(d.ings, notWantedIngs).length

            return ({
                ...d,
                ...{
                    usable: isUsable && isWanted && isNotNotWanted
                }
            })
        })
        .filter(d => d.usable)
}

export {
    getRandomDrink, disableDrinksByIngs, selectDrinks
}