import React from 'react';
import { connect } from 'react-redux';
import { ingCustomAdd, ingCustomRem, ingCustomRemIdx } from '../ings-actions'
import './ing-custom.scss'

function dispatchToProps(dispatch) {
    return {
        addIng: ing => dispatch(ingCustomAdd(ing)),
        remIng: ing => dispatch(ingCustomRem(ing)),
        remIngByIdx: idx => dispatch(ingCustomRemIdx(idx))
    };
}

function IngsCustom({ ing, addIng, remIng, remIngByIdx }) {

    return ing.name

    // return (<div className='ing-custom'>
    //     <span onClick={() => remIng(ing)} >&lt;&lt;usun </span>
    //     {ing.id}
    //     <span onClick={() => addIng(ing)}> dodaj&gt;&gt;</span>
    //     <span onClick={() => remIngByIdx()}>{ing.portion}</span>
    // </div>)
}

export default connect(null, dispatchToProps)(IngsCustom);
